body {
    margin: 0;
  }
  h1, h2, p, ul, li {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }

  a {
    text-decoration: none;
    color: rgb(124, 124, 124);
  }

  a:hover {
    color: #000;
    transition-duration: 0.6s;
  }
  
  img {
    margin-top: 40px;
    margin-bottom: 40px;
    width: 900px;
  }
  
  h2 {
    font-size: 20px;
    font-weight: 800px;
  }

  h1 {
    font-size: 2.5em;
    font-weight: 300;
    width: 800px;
    color: rgb(46, 46, 46);
    margin-left: auto;
    margin-right: auto;
  }
  .content {
    background-color: #FFF;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }
  .active {
    color: rgb(0, 0, 0);
  }
  #main {
    width: 900px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
  }
  #container {
      /* position: relative; */
      display: flex;
      flex-wrap: wrap;
      width: 800px;
      /* height: 700px; */
      /* background-color: black; */
      margin-left: auto;
      margin-right: auto;
      padding: 10px;
  }

  .el {
    width: 380px;
    height: 380px;
    margin: 10px;
    background-color: rgb(0, 0, 0);
    transition: .5s ease;
  }

  .el:hover {
    opacity: 0.3;
  }

  .menu {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    float: right;
    margin-top: 20px;
    font-weight: 700;
    font-size: 0.9em;
    word-spacing: 2em;    
  }

  /* .menu a {
    color: rgb(206, 7, 7);
    word-spacing: 2em;
  } */

  .logo {
    display: inline-block;
    letter-spacing: -0.8px;
    color: rgb(46, 46, 46);
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
  }

  .header {
    width: 900px;
    display: inline-block; 
  }

  #up_margin {
    height: 30px;
  }

  #down_margin {
    height: 100px;
  }

p {
  color: rgb(43, 43, 43);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 0.9rem;
  line-height: 1.3rem;
}

